<template>
  <div class="player">
    <h1></h1>
    <div id="player"></div>
  </div>
</template>

<script>
export default {
  name: "Player",
  data() {
    return {}
  },
  methods: {
    check_login() {
      // 
      let token = localStorage.user_token || sessionStorage.user_token;
      if (!token) {
        this.$alert("").then(() => {
          this.$router.push("/user/login");
        });
        return false; // 
      }
      return token;
    },
  },
  // created ajax/
  // mounted js
  mounted() {
    let jwt_token = this.check_login();
    let user_name = localStorage.user_name || sessionStorage.user_name;
    // 1. 
    // 2. 
    let vid = "123d56b2b5ff2a46cc7b856070cddf26_1"; // this.$router.query.vid;
    console.log(vid)
    let self = this;

    var player = polyvPlayer({
      wrap: '#player',
      width: document.documentElement.clientWidth - 260, // 
      height: document.documentElement.clientHeight, // 
      forceH5: true,
      vid: vid,
      code: user_name, // 
      // 
      playsafe: function (vid, next) { // token
        self.$axios.get(`${self.$settings.HOST}/course/player/`, {
          params: {
            vid: vid,
          },
          headers: {
            "Authorization": "jwt " + jwt_token,
          }
        }).then(function (response) {
          // token()
          next(response.data);
        })
      }
    });
  },
  computed: {}
}
</script>

<style scoped>

</style>



